<template>
  <div class="component">
    <div class="container">
      <div class="row p-4">
        <div class="col-md-6 col-sm-12 col-xs-12 offset-md-2 custom-panel">
          <form @submit.prevent="verifyPhoneNumber()">
            <h5 class="text-white p-2 text-center"> Verify Phone Number </h5>
            <p class="text-white p-2">Please enter your phone number for verification, a code will be sent to your phone
              number that
              will enable you to reset your password. </p>
            <div class="form-group mr-2">
              <input type="text" class="form-control"
                     v-model="forgetData.phone"
                     required
                     id="phone" aria-describedby="phoneHelp" placeholder="Enter Phone Number">
            </div>

            <button type="submit" class="btn btn-primary mr-1" :disabled="forgetData.phone.length < 10">
              Verify Phone Number
            </button>
          </form>
  </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {FORGOT_PASSWORD} from '../../api_routes'
  import axios from 'axios'

  export default {
    name: 'forgot_password',
    data () {
      return {
        forgetData: {
          phone: ''
        }
      }
    },
    methods: {
      verifyPhoneNumber () {
        axios.post(FORGOT_PASSWORD, this.forgetData).then((response) => {
          if (response.status === 200) {
            this.resetData.uuid = response.data.uuid
          }
        }, (error) => {
          console.log('UNABLE TO LOGIN USER', error)
        })
      }
    }
  }
</script>
